import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { Customer } from 'src/app/interfaces/customer';
import { Ticket } from 'src/app/interfaces/ticket';
import { User } from 'src/app/interfaces/user';
import { CustomersService } from 'src/app/services/customers.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
  selector: 'app-customer-detail',
  templateUrl: './customer-detail.component.html',
  styleUrls: ['./customer-detail.component.scss']
})
export class CustomerDetailComponent implements OnInit {

  customerId;
  currentCustomer:Customer;
  customerTickets: Ticket[];
  operatorTickets: User


  constructor(private customerService: CustomersService, private route: ActivatedRoute, private userService: UsersService) {


    route.params.subscribe(
      (params) => {
        var id = params['id'];
        this.customerId = params['id'];
        customerService.getSpecificCustomer(this.customerId).subscribe((cliente:Customer) =>{
          this.currentCustomer = cliente
          console.log(cliente)
          this.customerTickets = cliente.tickets

          cliente.tickets.forEach(ticket=>{
            var minutes=0
            var totMinWorked=0

            ticket.ticket_time.forEach(time=>{
              var startjob= new Date(time[0])
              var endjob= new Date(time[1])

              if(endjob>startjob){

                minutes=(endjob.getTime()-startjob.getTime())/(60*1000)
              }
              if(!isNaN(minutes)){
                totMinWorked+=minutes
              }
              
            })


            ticket.work_time= totMinWorked

          })
          
          

        })

        


      })
  }

  ngOnInit(): void {
  }

}
